import * as graphql from "graphql-yoga";
import { importSchema } from 'graphql-import';
import models from '../database/model';
import resolvers from './resolvers';
export var startServer = function (db) {
    var context = function (req) { return ({
        db: db,
        models: models,
        req: req.request
    }); };
    var options = {
        port: process.env.CLIENT_PORT || 3000,
        endpoint: '/graphql',
        playground: '/playground'
    };
    var server = new graphql.GraphQLServer({
        context: context,
        resolvers: resolvers,
        typeDefs: importSchema('server/graphql/typedefs/index.graphql')
    });
    server.start(options, function () {
        console.log("\uD83D\uDE80\uD83D\uDE80\uD83D\uDE80 Server is running on  http://localhost:" + process.env.CLIENT_PORT);
    });
    return server;
};



